html {
  --blue-color: #00acdc;
  --text-main-color: #060b35;
  --text-secondary-color: #616070;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.product_card[data-qa="card"] {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  display: block;
}

img {
  overflow-clip-margin: content-box;
  overflow: clip;
}

.product_photo {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.product_name {
  color: var(--text-main-color);
  margin: 0 0 4px;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 18px;
  display: block;
}

.product_code {
  color: var(--text-secondary-color);
  margin-bottom: 16px;
  font-size: 10px;
  line-height: 14px;
  display: block;
}

.note {
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 24px;
  display: flex;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars .stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:first-child, .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.reviews {
  text-align: right;
  color: var(--text-main-color);
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  line-height: 14px;
  display: block;
}

.price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.price_name {
  color: var(--text-secondary-color);
  font-size: 12px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  display: block;
}

.price_number {
  color: var(--text-main-color);
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 18px;
  display: block;
}

.button_buy {
  text-align: center;
  box-sizing: border-box;
  border: 1px solid var(--blue-color);
  background-color: var(--blue-color);
  color: #fff;
  text-transform: uppercase;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  height: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 16px;
  display: flex;
}

.button_buy:hover {
  color: var(--blue-color);
  background-color: #fff;
}

